import React, { useEffect } from 'react';
import './App.css';
import { Route } from 'react-router-dom';
import { WorkersPage } from './pages/workers/WorkersPage/WorkersPage';
import { Home } from './pages/Home/Home';
import WorkerDetails from './pages/workers/WorkerDetails/WorkerDetails';
import Login from './pages/Login/Login';
import { getUser } from './services/userService';
import { useDispatch } from 'react-redux';
import * as actionTypes from './store/actions/actionsUser/actionTypes';

const App = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    console.log('INIT APP');
    const foo = async () => {
      try {
        const { data } = await getUser();
        if (data) {
          dispatch({ type: actionTypes.SET_USER, payload: data })
        }
      } catch (e) {
        console.log('e', e);
      }
    };
    foo()
  }, [dispatch]);

  return (
    <section>
      <header className="navbar-app">
        <div></div>
        <div>מאגר הבייביסיטר הגדול בישראל</div>
      </header>
      <Route path="/" exact component={Home} />
      <Route path="/login" exact component={Login} />
      <Route path="/workers/:cityId" exact component={WorkersPage} />
      <Route path="/workers/:cityId/:workerId" exact component={WorkerDetails} />
    </section>
  );
}

export default App;
